import React, {useState, useEffect} from "react";
import '../../../css/admin/admin.css';
import {Accordion, Col, Container, Row} from "react-bootstrap";
import AdminAsideMenu from "../AdminAsideMenu";
import {Link, useLocation } from "react-router-dom";
import axios from "axios";
import FaqPagination from "./proc/FaqPagination";
import FaqCate from "./proc/FaqCate";

// 년,월,일만 출력
function formatDate(dateString) {
    const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
    const formattedDate = new Date(dateString).toLocaleDateString(undefined, options);
    return formattedDate;
}
function FasList(){
    // 데이터 호출
    const [listData, setListData] = useState([]); // 현재 게시글 목록
    const [pageData, setPageData ] =useState({}); // 페이지네이션
    const [page, setPage] = useState(1); // 페이지 번호
    // 카테고리 추출
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const cate = searchParams.get('cate');
    // 수정될 데이터
    const [selectedItem, setSelectedItem] = useState(null);

    // useEffect로 한번 실행된 데이터를 빈배열에 담게 해 무한반복 방지
    useEffect(() => {
        axios.get(`/api/admin/faq/adminFaqList?cate=${cate || ''}&page=${page}`)
            .then(res => {
                setListData(res.data.csList);
                setPageData(res.data);
                setPage(res.data.pg);
            })
            .catch(err => {
                console.error("데이터를 찾을 수 없습니다. error : " + err);
            });
    }, [cate, page]);

    /* ------------ 수정모드 -------------- */
    const handleModify = (item, index) => {
        // 수정할 데이터 가져오기
        setSelectedItem({ item, index });
    };
    useEffect(() => {
        // selectedItem이 변경될 때마다 로그를 출력
        console.log(selectedItem);
    }, [selectedItem]);

    // 수정된 데이터 전송
    const handleModifyComplete = async () => {
        try {
            await axios.put('/api/admin/faq/adminFaqModify', {
                cno: selectedItem.item.cno,
                content: selectedItem.item.content,
            });
        } catch (error) {
            console.error('데이터 전송 실패:', error);
        }
    };
    
    // 수정 모드 폼
    const modifyBoard = selectedItem && (
        <Accordion.Body>
            <form>
                <p>
                    <textarea>{selectedItem.item.content}</textarea>
                </p>
                <p>
                    <button type="submit" className="btnModify" onClick={handleModifyComplete}>완료</button>
                </p>
            </form>
        </Accordion.Body>
    );


    return (
        <>
            <Accordion>
                {listData.map((item, index) => (
                    <Accordion.Item key={index} eventKey={index.toString()}>
                        <Accordion.Header>[{item.cateName}]{item.title}<span className="date">{formatDate(item.rdate)}</span></Accordion.Header>
                        {selectedItem && selectedItem.index === index ? modifyBoard : (
                            <Accordion.Body>
                                <p>
                                    {item.content.split('\n').map((line, i) => (
                                        <React.Fragment key={i}>
                                            {line}
                                            <br/>
                                        </React.Fragment>
                                    ))}
                                </p>
                                <p>
                                    <button className="btnModify" onClick={() => handleModify(item,index)}>수정</button>
                                    <button className="btnDelete">삭제</button>
                                </p>
                            </Accordion.Body>
                        )}
                    </Accordion.Item>
                ))}
                <Accordion.Item eventKey="4">
                    <Accordion.Header>[이용문의] 매장에서 몇 명까지 식사가 가능한가요?<span className="date">2023-11-01</span></Accordion.Header>
                    <Accordion.Body>
                        <p>
                            <textarea></textarea>
                        </p>
                        <p>
                            <button className="btnModify">완료</button>
                        </p>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            <FaqPagination
                page={page}
                setPage={setPage}
                pageData={pageData}
            />
        </>
    )
}
function AdminFaqList() {
    return (
        <section id="admin">
            <Container id="adminFaqList">
                <Row className="justify-content-center">
                    {/* aside menu */}
                    <AdminAsideMenu/>
                    <Col>
                        <div className="faqRegisterButton">
                            <Link to="/admin/faq/adminFaqRegister" className="btnWrite">자주묻는질문 등록</Link>
                        </div>
                        <FaqCate/>
                        <FasList/>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default AdminFaqList;